<template>
  <div class="guidance-container">
    <el-card style="margin-bottom: 30px;">
      <div class="express-container">
        <div class="express-item" @click="$router.push({ name: 'AddRecipes' })">
          新建食谱模板
        </div>
      </div>
      <div class="search-container">
        <el-row>
          <el-col :span="6">
            <div class="search-item">
              <div>模板名称</div>
              <div>
                <el-input
                  v-model="searchForm.name"
                  placeholder="请输入模板名称"
                  size="mini"
                />
              </div>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="search-item">
              <div>创建人</div>
              <div>
                <el-select
                  v-model="searchForm.uid"
                  size="mini"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in allCreaters"
                    :key="item._id"
                    :label="item.name"
                    :value="item._id"
                  />
                </el-select>
              </div>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="search-time search-date-picker">
              <div>
                创建时间
              </div>
              <div>
                <el-date-picker
                  v-model="searchTime"
                  size="mini"
                  type="datetimerange"
                  range-separator="-"
                  start-placeholder="请选择时间范围起始"
                  end-placeholder="请选择时间范围结束"
                />
              </div>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <div class="search-item">
              <div>状态</div>
              <div>
                <el-select v-model="searchForm.status" size="mini">
                  <el-option
                    v-for="item in statusOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  />
                </el-select>
              </div>
            </div>
          </el-col>
          <el-col :span="18">
            <div class="recipes-button">
              <div>
                <el-button type="primary" size="mini" @click="search"
                  >搜索</el-button
                >
              </div>
              <div>
                <el-button size="mini" @click="reset">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-card>
    <el-card>
      <div class="table-title">
        食谱模版列表
      </div>
      <el-table :data="tableData" height="450px" style="width: 100%">
        <el-table-column
          prop="title"
          label="模板名称"
          align="center"
          header-align="center"
        />
        <el-table-column label="创建人" align="center" header-align="center">
          <template slot-scope="scope">
            {{ scope.row.dietician.name }}
          </template>
        </el-table-column>
        <el-table-column label="创建时间" align="center" header-align="center">
          <template slot-scope="scope">
            {{ new Date(scope.row.created_at) | formatTime() }}
          </template>
        </el-table-column>
        <el-table-column label="状态" align="center" header-align="center">
          <template slot-scope="scope">
            {{ scope.row.status | recipesStatus }}
          </template>
        </el-table-column>
        <el-table-column align="left" header-align="center" label="操作">
          <template slot-scope="scope">
            <el-button
              type="text"
              style="margin-left:10px;"
              :class="scope.row.status === 'online' ? 'online-style' : ''"
              @click="publish(scope.row)"
              >单击{{
                scope.row.status === 'online' ? '下线' : '发布'
              }}</el-button
            >
            <el-button size="mini" type="text" @click="handleCheck(scope.row)"
              >查看</el-button
            >
            <el-button
              v-if="scope.row.status !== 'online'"
              :disabled="!scope.row.isSelfCreate"
              size="mini"
              type="text"
              @click="handleEdit(scope.row)"
              >编辑</el-button
            >
            <el-button
              v-if="scope.row.status !== 'online'"
              :disabled="!scope.row.isSelfCreate"
              type="text"
              size="mini"
              @click="handleDelete(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div style="margin-top: 10px;text-align: center;">
        <el-pagination
          background
          :current-page.sync="currentPage"
          :page-sizes="[10, 20, 50]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
      <el-dialog
        :title="dialogText.title"
        :visible.sync="dialogVisible"
        :center="true"
        width="22%"
        @closed="handleClose"
      >
        {{ dialogText.desc }}
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="handleConfirm">{{
            dialogText.button
          }}</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import { getRecipesList, publishRecipes, deleteRecipes } from '@/api/recipes'
export default {
  name: 'Guidance',
  components: {},
  data() {
    return {
      dietician: this.$store.state.user.name,
      currentId: '',
      allCreaters: [],
      dialogText: {
        title: '',
        desc: '',
        button: ''
      },
      dialogVisible: false,
      currentPage: 1,
      pageSize: 10,
      total: 0,
      tableData: [],
      searchForm: {
        name: '',
        nickname: '',
        uid: '',
        status: '',
        beginTime: '',
        endTime: ''
      },
      searchTime: null,
      statusOptions: [
        {
          value: 'save',
          label: '已保存'
        },
        {
          value: 'online',
          label: '已发布'
        },
        {
          value: 'offline',
          label: '已下线'
        }
      ]
    }
  },
  mounted() {
    this.getRecipesList()
  },
  methods: {
    // 点击弹框的确定按钮
    handleConfirm() {
      const confirmType = this.dialogText.button
      switch (confirmType) {
        case '确认删除':
          deleteRecipes(this.currentId).then(res => {
            if (res.code === 20000) {
              this.dialogVisible = false
              this.reset()
            }
          })
          break
        default:
          publishRecipes(this.currentId).then(res => {
            if (res.code === 20000) {
              this.dialogVisible = false
              this.reset()
            }
          })
      }
    },
    publishStatus(status) {
      return status === 'online'
    },
    handleClose() {
      // 刷新列表
      this.reset()
    },
    publish(row) {
      this.currentId = row._id
      this.dialogVisible = true
      if (row.status === 'offline' || row.status === 'save') {
        this.dialogText = {
          title: '发布提示',
          desc: `操作成功后，该模板数据将更新在新建定制食谱任务的下拉菜单中，可在创建任务时供营养师 [${this.dietician}] 选择，请确认后操作！`,
          button: '确认发布'
        }
      } else {
        this.dialogText = {
          title: '下线提示',
          desc: `操作成功后，该模板数据将从营养师 [${this.dietician}] 的新建定制食谱任务的下拉菜单中移除，不可被选择，请确认后操作！`,
          button: '确认下线'
        }
      }
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.search()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.search()
    },
    handleEdit(row) {
      this.$router.push({
        path: '/system-settings/recipes/edit',
        query: { id: row._id }
      })
    },
    handleCheck(row) {
      this.$router.push({
        path: '/system-settings/recipes/check',
        query: { id: row._id }
      })
    },
    handleDelete(row) {
      this.currentId = row._id
      this.dialogText = {
        title: '删除提示',
        desc: '操作成功后，该食谱模板将从食谱模板列表删除，请确认后操作！',
        button: '确认删除'
      }
      this.dialogVisible = true
    },
    async getRecipesList() {
      // 请求查询食谱的接口
      const { data } = await getRecipesList(this.searchForm)
      this.total = data.total
      this.tableData = data.list
      this.allCreaters = data.allCreaters
    },
    // 根据搜索条件查询任务列表
    search() {
      this.searchForm.beginTime = this.searchTime ? this.searchTime[0] : ''
      this.searchForm.endTime = this.searchTime ? this.searchTime[1] : ''
      this.searchForm.pageSize = this.pageSize
      this.searchForm.currentPage = this.currentPage
      // 查询食谱模版列表
      this.getRecipesList()
    },
    // 重置查询条件
    reset() {
      this.searchForm = {
        name: '',
        uid: '',
        status: '',
        beginTime: '',
        endTime: ''
      }
      this.searchTime = null
      this.search()
    }
  }
}
</script>
<style lang="scss">
.search-item {
  .el-input {
    width: 180px;
  }
}
.tab-container {
  .el-badge__content.is-fixed {
    top: 10px;
  }
}
</style>

<style scoped lang="scss">
@import '@/styles/healthManagement/page.scss';
.recipes-button {
  display: flex;
  justify-content: flex-end;
  margin-right: 260px;
  div {
    margin-right: 10px;
  }
}
</style>
